export const generalStates = {
	airports: [],
	quotes: []
}

export const generalReducer = (state, action) => {
	switch (action.type) {
		case 'AIRPORTS_DATA':
			return {
				...state,
				airports: action.data
			}
			case 'QUOTES_DATA':
				return {
					...state,
					quotes: action.data
				}
			case 'DELETED_QUOTE':
				return {
					...state,
					quotes:action.quotes
				}
		default:
			throw new Error("Unexpected action");
	}
};