/*------------------------UPDATE ENDPOINTS-----------------------*/
async function updateQuote(id, data) {
	try {
		const response = await apiIntance.put('/quotes/'+ id, data);
		return response.status;
	} catch (error) {
		console.warn('Error with updateQuote API');
	}
}

