async function deleteTour(id) {
	try {
		const response = await apiIntance.delete('/tours/'+ id);
		return response.status;
	} catch (error) {
		console.warn('Error with deleteTour API');
	}
}

export {
	getAirports,
	getPendindQuotes,
	getInvoices,
	getTours,
	createQuote,
	createInvocie,
	createTour,
	updateQuote,
	updateInvoice,
	updateTour,
	deleteQuote,
	deleteInvoice,
	deleteTour
}