import React, { useContext, useState } from 'react';
import { StoreContext } from '../../context/store/storeContext';
import { Table, Card, Button, Modal, notification } from 'antd';
import { deleteQuote, getPendindQuotes, updateQuote } from '../../api';
import EditQuoteModal from '../EditQuoteModal'

const { confirm } = Modal


const columns = [,
	{
		title: 'Actions',
		dataIndex:'actions',
		key: 'actions',
		fixed: 'left',
		render: (actions) => (
			<div className='actions'>
				<Button key='edit' onClick={actions.edit} type="dashed">
					<i className="fas fa-pencil-alt"></i>
				</Button>
				<Button key='delete' onClick={actions.delete} danger>
					<i className="fas fa-trash"></i>
				</Button>
				<Button key='transform' onClick={actions.service} type='primary'>Register Service</Button>
			</div>
		)
	},
	{
		title: 'Id',
		dataIndex: 'quote_id',
		key: 'id',
	},
	{
		title: 'Name',
		dataIndex: 'name',
		key: 'name',
	},
	{
		title: 'Phone',
		dataIndex: 'phone',
		key: 'pone',
	},
	{
		title: 'Depart Date',
		dataIndex: 'departDate',
		key: 'depart_date',
	},
	{
		title: 'Return Date',
		dataIndex: 'returnDate',
		key: 'return_date',
	},
	{
		title: 'People',
		dataIndex: 'n_people',
		key: 'n_people',
	},
	{
		title: 'Transportation',
		dataIndex: 'transportation',
		key: 'transportation',
	},
	{
		title: 'Amount',
		dataIndex: 'Amount',
		key: 'Amount',
	},
	{
		title: 'Departure',
		dataIndex: 'departure',
		key: 'departure',
	},
	{
		title: 'Arrival',
		dataIndex: 'arrival',
		key: 'arrival',
	},
	{
		title: 'Description',
		dataIndex: 'description',
		key: 'description',
	}
];

const iniState = {
	isLoading: false,
	isEdit: false,
	editData: {

	}
}

const QuoteList = () => {
	const { state, actions } = useContext(StoreContext);
	const [_localState, setState] = useState(iniState)
	const dispatch = actions.generalActions.quotesData;
	const dataSource = state.generalStates.quotes.map(el => {
		el.departDate = getFormatedDate(el.depart_date)
		el.returnDate = getFormatedDate(el.return_date)
		el.key = el.quote_id
		el.actions = {
			delete: () => { 
				showDeleteConfirm(handleDelete, el.quote_id, _localState, setState, dispatch)
			 },
			edit: () => { 
				setState(Object.assign({}, _localState, { isEdit: true, editData: el }))
			},
			service: () => { handleService(el.quote_id, _localState, setState) }
		}
		return el
	})
	console.log(_localState.isEdit);
	return (
		<Card 
			title="Pending Quotes"
			extra={<i className="fas fa-expand-arrows-alt"></i>}
			className='quotes-list'
		>
			<Table loading={_localState.isLoading} dataSource={dataSource} columns={columns} />
		<EditQuoteModal
			visible={_localState.isEdit}
			data={_localState.editData}
			onSubmit={(resp) => {
				console.log(resp);
				handleEdit(resp, _localState, setState, dispatch)
			}}
			onCancel={() => {
				setState(Object.assign({}, _localState, {isEdit: false})) 
			}}
		/>
		</Card>
	)
}
/**
 * get short date
 * @param {String} strDate 
 * @returns 
 */
function getFormatedDate(strDate) {
	const date = new Date(strDate);
	return `${date.getMonth()}/${date.getDay()}/${date.getFullYear()}`
}


async function handleEdit(el, _localState, setState, dispatch) {
	let updateState = Object.assign({}, _localState, {isEdit: false, editData: {}})
	setState(Object.assign(updateState, { isLoading: true }))
	await updateQuote(el.quote_id, el);
	const quotes = await getPendindQuotes()
	setState(Object.assign(updateState, { isLoading: false }))
	dispatch(quotes)
	openNotification('Success Edit Quote #' + el.quote_id)
}
/**
 * Handle Delete Action
 */
async function handleDelete(id, _localState, setState, dispatch) {
	setState(Object.assign({}, _localState, { isLoading: true }))
	const resp = await deleteQuote(id)
	const quotes = await getPendindQuotes()
	console.log(resp);
	setState(Object.assign({}, _localState, { isLoading: false }))
	dispatch(quotes)
	openNotification('Success delete Quote #' + id)

}

function handleService(id) {
	console.log(id);
}
/**
 * Show confirmation on delete
 * @param {Fucntion} handler 
 * @param  {...any} params 
 */
function showDeleteConfirm(handler, ...params) {
	confirm({
		title: 'Are you sure delete this Quote?',
		content: 'Will delete this quote permanently',
		okText: 'Yes',
		okType: 'danger',
		cancelText: 'No',
		onOk() {
			handler(...params)
		},
		onCancel() {
			console.log('Cancel');
		},
	});
}
/**
 * Open notification
 * @param {String} title 
 */
function openNotification (title) {
	notification.open({
		message: title,
		placement: 'bottomRight',
		onClick: () => {
			console.log('Notification Clicked!');
		},
	});
};

export default QuoteList