import React, { useContext, useState, useEffect } from 'react';
import { StoreContext } from '../../context/store/storeContext';
import { Modal, Select, DatePicker, Space, InputNumber, Input } from 'antd';
import moment from 'moment';


const { Option } = Select;
const { RangePicker } = DatePicker;

const EditQuoteModal = (props) => {
	const { state } = useContext(StoreContext);
	const [_localState, setState] = useState(Object.assign({}, props.data, { nPeople: props.data.n_people }));
	useEffect(() => {
		if ((JSON.stringify(_localState) === '{}')&&
			(JSON.stringify(props.data) !== '{}')
		) {
			console.log('update state');
			setState(Object.assign({}, props.data, { nPeople: props.data.n_people }))
		}
	})
	return (
		<Modal
		title={'Edit Quote #'+ _localState?.quote_id}
		visible={props.visible}
		className='edit-modal'
		onOk={() => { 
			props.onSubmit(
				converAirportToID(state.generalStates.airports,_localState)
			)
		}}
		onCancel={() => { 
			props?.onCancel()
			setState({})
		}}
	>
		<Input
			name='name'
			placeholder="Name"
			value={_localState.name}
			onChange={(ev) =>{ mapInputs(ev, _localState, setState) } }
			/>
		<Input
			name='phone'
			placeholder="Phone"
			value={_localState.phone}
			onChange={(ev) =>{ mapInputs(ev, _localState, setState) } }
		/>
		<Select
			defaultValue="from"
			value={_localState.departure}
			onChange={(val) => {
				console.log(val);
				setState(Object.assign({}, _localState, { departure: val }))
			}}
	>
			<Option value="from">From</Option>
			{ getAirportsOptions(state.generalStates.airports) }
		</Select>
		<Select
			defaultValue="destination"
			value={_localState.arrival}
			onChange={(val) => {
				setState(Object.assign({}, _localState, { arrival: val }))
			}}
		>
			<Option value="destination">Destination</Option>
			{ getAirportsOptions(state.generalStates.airports) }
		</Select>
		<Space direction="vertical" size={12}>
			<RangePicker
				defaultValue={[
					moment(_localState.departDate, 'MM/DD/YYYY'),
					moment(_localState.returnDate, 'MM/DD/YYYY')
				]}
				onChange={ev => { handleDatePicker(ev, _localState, setState) } }
			/>
		</Space>
		<div className='people-box'>
			<label>People</label>
			<InputNumber
				value={_localState.nPeople}
				min={1}
				onChange={(val) => {
					setState(Object.assign({}, _localState, { nPeople: val }))
				}}
			/>
		</div>
		{ getTransportationOptions(_localState, setState) }
		<Input
			name='amount'
			placeholder= 'Amount'
			value={_localState.amount}
			onChange={(ev) => { mapInputs(ev, _localState, setState) } }
		/>
		<Input
			name='description'
			placeholder= 'Description'
			value={_localState.description}
			onChange={(ev) => { mapInputs(ev, _localState, setState) } }
		/>
		</Modal>
	)
}
/**
 * Util function
 */
function converAirportToID(airports, state) {
	let _newSate = state
	if( isNaN(parseInt(state.departure)) ) {
		airports.forEach(a => {
			if(a.name === state.departure) { _newSate.departure = a.airport_id }
		});
	}
	if( isNaN(parseInt(state.arrival)) ) {
		airports.forEach(a => {
			if(a.name === state.arrival) { _newSate.arrival = a.airport_id }
		});
	}
	return _newSate
}

/**
 * Map input to state
 */
function mapInputs(ev, state, setState) {
	const name = ev.target.name
	const data = ev.nativeEvent.data
	if(data !== null) {
		setState(Object.assign({}, state, { [name]: state[name] + data }))
	} else if(ev.nativeEvent.inputType === 'deleteContentBackward') {
		let val = state[name];
		val = val.split(''); val.pop(); val =val.join('');
		setState(Object.assign({}, state, { [name]: val }))
	}
}

/**
 * get the airports options
 * @param {Arry} list 
 * @returns JSX
 */
 function getAirportsOptions(list) {
	return list.map((item, i) => {
		return (
			<Option key={i} value={item.airport_id}>{item.name}</Option>
		)
	})
}
/**
 * Handle picking a range date
 */
function handleDatePicker(ev, state,setState) {
	console.log(ev);
	if (ev === null) return
	setState(Object.assign(
		{},
		state,
		{
			departDate: ev[0]._d.getTime(),
			returnDate: ev[1]._d.getTime() 
		}
	))
}

/**
 * Get transportation Select
 * @returns JSX
 */
 function getTransportationOptions(state, setState) {
	return (
		<Select
		defaultValue="transportation"
		disabled={state.isLoading}
		onChange={(val) => {
			setState(Object.assign({}, state, { transportation: val }))
			}}
		name='transportation'
		>
			<Option value='transportation'>Transportation</Option>
			<Option value='Car Rental'>Car Rental</Option>
			<Option value='Cab Service'>Cab Service</Option>
			<Option value='Limousines'>Limousines</Option>
			<Option value='Hotel Shuttles'>Hotel Shuttles</Option>
			<Option value='Bus Tour Operators'>Bus Tour Operators</Option>
		</Select>
	)
}

export default  EditQuoteModal