import React, { useState, useContext } from 'react';
import { Card, Input, Select, DatePicker, Space, InputNumber, Button, notification } from 'antd';
import { StoreContext } from '../../context/store/storeContext';
import { createQuote, getPendindQuotes } from '../../api';

const { Option } = Select;
const { RangePicker } = DatePicker;

let iniState = {
	name: '',
	phone: '',
	description: '',
	amount: '',
	nPeople: 1,
	isLoading: false
}

const QuickCard = () => {
	const [_localState, setState] = useState(iniState)
	const { state, actions } = useContext(StoreContext)
	const { airports } = state.generalStates;
	const isAirportReady = !airports?.length > 0
	const { isLoading } = _localState;
	const isBtnReady = Object.keys(_localState).length === 11
	return (
		<Card 
			title="Quick Quote"
			extra={<i className="fas fa-expand-arrows-alt"></i>}
			className='quick-quote'
		>
			<Input
				 name='name'
				 placeholder="Name"
				 value={_localState.name}
				 onChange={(ev) => { mapInputs(ev, _localState, setState) } }
				 disabled={isLoading}
			/>
			<Input
				name='phone'
				placeholder="Phone"
				value={_localState.phone}
				onChange={(ev) => { mapInputs(ev, _localState, setState) } }
				disabled={isLoading}
			/>
			<Select
				defaultValue="from"
				disabled={isLoading}
				loading={isAirportReady}
				onChange={(val) => {
					console.log(val);
					setState(Object.assign({}, _localState, { departure: val }))
				}}
			>
					<Option value="from">From</Option>
					{ getAirportsOptions(airports) }
			</Select>
			<Select
				defaultValue="destination"
				disabled={isLoading}
				loading={isAirportReady}
				onChange={(val) => {
					setState(Object.assign({}, _localState, { arrival: val }))
				}}
			>
				<Option value="destination">Destination</Option>
				{ getAirportsOptions(airports) }
			</Select>
			<Space direction="vertical" size={12}>
				<RangePicker
					disabled={isLoading}
					onChange={ev => { handleDatePicker(ev, _localState, setState) } }
				/>
			</Space>
			<div className='people-box'>
				<label>People</label>
				<InputNumber
					disabled={isLoading}
					value={_localState.nPeople}
					min={1}
					onChange={(val) => {
						setState(Object.assign({}, _localState, { nPeople: val }))
					}}
				/>
			</div>
			{ getTransportationOptions(_localState, setState) }
			<Input
				name='amount'
				placeholder= 'Amount'
				value={_localState.amount}
				disabled={isLoading}
				onChange={(ev) => { mapInputs(ev, _localState, setState) } }
			/>
			<Input
				name='description'
				placeholder= 'Description'
				value={_localState.description}
				disabled={isLoading}
				onChange={(ev) => { mapInputs(ev, _localState, setState) } }
			/>
			<Button
				onClick={ () => {
					handleSubmit(_localState,setState, actions.generalActions.quotesData)
				} }
				disabled={isLoading || !isBtnReady}
				loading={isLoading}
				type='primary'>
					Create Quote
			</Button>
		</Card>
	)
}
/**
 * get the airports options
 * @param {Arry} list 
 * @returns JSX
 */
function getAirportsOptions(list) {
	return list.map((item, i) => {
		return (
			<Option key={i} value={item.airport_id}>{item.name}</Option>
		)
	})
}
/**
 * Handle picking a range date
 */
function handleDatePicker(ev, state,setState) {
	console.log(ev);
	if (ev === null) return
	setState(Object.assign({}, state,{ departDate: ev[0]._d.getTime(), returnDate: ev[1]._d.getTime() }))
}

async function handleSubmit(state, setState, dispatch){
	console.log(state);
	setState(Object.assign({},state,{ isLoading: true }))
	const status = await createQuote(state);
	const quotes = await getPendindQuotes();
	dispatch(quotes)
	console.log(status);
	openNotification('Success, new Quote added')
	setState(iniState)
}
/**
 * Map input values to local state
 * */
function mapInputs(ev, state, setState) {
	const name = ev.target.name
	const data = ev.nativeEvent.data
	if(data !== null) {
		setState(Object.assign({}, state, { [name]: state[name] + data }))
	} else if(ev.nativeEvent.inputType === 'deleteContentBackward') {
		let val = state[name];
		val = val.split(''); val.pop(); val =val.join('');
		setState(Object.assign({}, state, { [name]: val }))
	}
}
/**
 * Get transportation Select
 * @returns JSX
 */
function getTransportationOptions(state, setState) {
	return (
		<Select
		defaultValue="transportation"
		disabled={state.isLoading}
		onChange={(val) => {
			setState(Object.assign({}, state, { transportation: val }))
			}}
		name='transportation'
		>
			<Option value='transportation'>Transportation</Option>
			<Option value='Car Rental'>Car Rental</Option>
			<Option value='Cab Service'>Cab Service</Option>
			<Option value='Limousines'>Limousines</Option>
			<Option value='Hotel Shuttles'>Hotel Shuttles</Option>
			<Option value='Bus Tour Operators'>Bus Tour Operators</Option>
		</Select>
	)
}

/**
 * Open notification
 * @param {String} title 
 */
 function openNotification (title) {
	notification.open({
		message: title,
		placement: 'bottomRight',
		onClick: () => {
			console.log('Notification Clicked!');
		},
	});
};


export default QuickCard