import React , { useContext, useEffect } from "react";
import { StoreContext } from './context/store/storeContext';
import './sass/index.scss';
import MainHeader from './components/MainHeader/index';
import SideMenu from './components/SideMenu';
import Dashboard from './components/Dashboard';
import "antd/dist/antd.css";
import { getAirports, getPendindQuotes } from './api';

const App = () => {
	const { actions } = useContext(StoreContext);
	useEffect(() => {
		getData(actions.generalActions);
	}, [])
	return (
		<div className="dashboard-wrapper">
			<MainHeader></MainHeader>
			<SideMenu></SideMenu>
			<Dashboard></Dashboard>
		</div>
	);
};
/**
 * First fetch for the data
 */
async function getData(generalActions) {
	const { airportsData, quotesData } = generalActions;
	const airports = await getAirports();
	if (airports) airportsData(airports)
	const quotes = await getPendindQuotes();
	if (quotes) quotesData(quotes)
}

export default App;
